.caja{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 90px 0;
    padding: 15px;
}
.error{
    display: flex;
    flex-direction: column;
}
.error-404{
    display: flex;
    justify-content: center;
    font-size: 60px;
    color: red;
    margin-top: 10px;
}
.pag{
    margin: 40px 0;
    padding: 6px;
    font-size: 30px;
}
.button{
    margin: 0px 100px;
    color: black;
    border: 2px solid blue;
    border-radius: 8px;
    font-size: 16px;
    font-weight: bold;
    width: 120px;
    height: 40px;
    cursor: pointer;
}
.button:hover{
    background-color: rgb(253, 105, 105);
}

@media(max-width:  400px){
    .error-404{
        font-size: 40px;
    }
    .pag{
        margin: 20px 50px;
        font-size: 20px;
    }
}
